.pupbox {
  margin: 50px auto 100px;
  width: 600px;
  border: 1px solid #eeeeee;
  padding: 13px 20px;
  border-radius: 5px;
  padding: 20px 0 50px; }
  .pupbox .login-title:before {
    display: inline-block;
    margin-right: 10px;
    transform: translate(0, 3px);
    content: url(images/login-img.png); }
  .pupbox .padding-lr {
    padding: 0 20px; }
    .pupbox .padding-lr p {
      padding: 0 20px;
      color: #b5b5b5; }
    .pupbox .padding-lr .social-box {
      display: flex;
      justify-content: space-around; }
      .pupbox .padding-lr .social-box button {
        flex-basis: 50%; }
      .pupbox .padding-lr .social-box .fb {
        background: url(images/fb.png) no-repeat 15px center;
        background-color: #23599b;
        margin-right: 10px; }
      .pupbox .padding-lr .social-box .google {
        background: url(images/google.png) no-repeat 15px center;
        background-color: #dd4b39; }
    .pupbox .padding-lr .login-box input {
      margin-bottom: 10px;
      width: 100%; }
    .pupbox .padding-lr ul {
      color: #f2959f;
      display: flex;
      justify-content: space-around; }
      .pupbox .padding-lr ul li {
        width: 32%; }
    .pupbox .padding-lr input[type=submit] {
      color: white;
      width: 200px;
      background: #e74c3c;
      margin-top: 50px; }

.phone-page .pupbox {
  padding: 20px 0; }
  .phone-page .pupbox .footer-next {
    text-align: right; }
    .phone-page .pupbox .footer-next a {
      color: #e74c3c; }
      .phone-page .pupbox .footer-next a:after {
        content: url(images/right2.png);
        display: inline-block;
        margin-left: 10px;
        transform: translate(0, 3px); }

.phone-page .flexbox {
  display: flex;
  justify-content: space-around;
  margin: 10px 0;
  justify-content: flex-start;
  align-items: center; }
  .phone-page .flexbox label {
    flex-basis: 10%; }
  .phone-page .flexbox select {
    background-position: 90% center;
    flex-basis: 34%; }
  .phone-page .flexbox .phonebox {
    flex-basis: 90%;
    display: flex;
    justify-content: space-around; }
    .phone-page .flexbox .phonebox .phone-code {
      border: 1px solid #eeeeee;
      padding: 13px 20px;
      border-radius: 5px;
      margin-right: -2px;
      flex-basis: 25%;
      border-radius: 5px 0 0 5px; }
    .phone-page .flexbox .phonebox .number {
      border-radius: 0 5px 5px 0;
      flex-basis: 75%; }
    .phone-page .flexbox .phonebox .enter-code {
      margin-right: 5px;
      flex-basis: 80%; }
    .phone-page .flexbox .phonebox .get-code {
      background-color: #362e2b;
      color: white;
      flex-basis: 20%; }
  .phone-page .flexbox p {
    padding: 0 20px;
    text-align: left; }

.last-page a {
  color: #f2959f; }

.last-page span, .last-page .garee-click {
  margin-top: 30px;
  display: block; }

.last-page span {
  color: #e74c3c; }
  .last-page span:before {
    display: inline-block;
    margin-right: 10px;
    transform: translate(0, 3px);
    transform: translate(0, 6px);
    content: url(images/warn.png); }

.last-page .genderbox {
  margin-bottom: 20px; }

.last-page .genderbox input, .last-page .genderbox label {
  display: inline-block;
  width: auto; }

.last-page .user:before {
  display: inline-block;
  margin-right: 10px;
  transform: translate(0, 3px);
  content: url(images/user.png); }

.last-page .flexbox {
  display: flex;
  justify-content: space-around;
  align-items: flex-start; }
  .last-page .flexbox select {
    background-position: 90% center; }
  .last-page .flexbox .first, .last-page .flexbox select {
    flex-basis: 35%;
    margin-right: 10px; }
  .last-page .flexbox .last, .last-page .flexbox .addressbox {
    flex-basis: 65%; }

.last-page p {
  padding: 0 20px;
  text-align: left; }

.last-page input {
  margin-bottom: 10px;
  width: 100%; }

.last-page #garee, .last-page #man, .last-page #female {
  width: auto; }
